.login-section {
  text-align: center;
  width: 100%;
  height: 100vh;
  .login {
    margin: 20px auto;
    max-width: 400px;
    min-height: 320px;
    max-height: 528px;
    border-radius: 3px;
    padding: 30px 40px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.12);
    .title {
      color: $clr1;
      font-weight: 700;
      margin: 1em 0;
    }
    form {
      display: flex;
      flex-direction: column;
      margin-top: 0em;
      input {
        outline-color: $clr2;
        border: 2px solid $clr4;
        height: 44px;
        padding: 0.5em;
        margin: 12px 10px;
        width: calc(100% - 20px);
        border-radius: $frmrad;
        transition: background-color 0.2s ease-in-out 0s,
          border-color 0.2s ease-in-out 0s;
      }
    }
    button {
      margin: 4px auto;
      width: calc(100% - 20px);
    }
    ul.sign-in {
      cursor: pointer;
      display: flex;
      justify-content: center;
      gap: 20px;
      li:first-child {
        margin-inline-start: -40px;
        list-style-type: none;
      }
    }
  }
}
